@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');

.containr {
background-color: #eca3a3;
font-family: 'Times New Roman', Times, serif;
font-size: smaller;
border-radius: 40px;
padding: 10px 30px;
display: flex;
flex-direction: row;
align-items: flex-start,

}

#container  :hover{
    background-color: aquamarine;
}

:root{
    
}
.login {
display: flex;
flex-direction: column;
align-items: center;
background-color:#f9dfc2;
font-family: 'Oswald', sans-serif;
height: 92.5vh;
}
.login table{
    height: 45vh;
    
    border: 8px solid saddlebrown;
    text-align: center;
    margin-top: 20vh;
    margin-bottom: 5vh;
    font-family: 'Oswald', sans-serif;
}

.login table tr th h1,h2, h3, h4{
    
    font-family: 'Oswald', sans-serif;
}

.login table tr td{
    text-align: center;
    padding: 8px;
}
.login table tr td input{
           
            height: 70px;
            width: 400px;
 
            border-radius: 10px;
            border-color: aliceblue;
           
            text-align: center;
            font-size: large;
}
.buttn{
    
    height: 70px;
    width: 300px;
    background-color:  #0d6efd;
    color: azure;
    font-size:large;
    border-radius: 10px;
    border-color: rgb(0, 145, 255);
}